.wp-block-zolo-pricing-table {
    box-sizing: border-box;
    position: relative;
    --zolo-item-wrap-border-width: 1px;
    --zolo-item-wrap-border-style: solid;
    --zolo-item-wrap-border-color: var(--zoloblocks-item-border-color);
    --zolo-head-content-padding: clamp(1.5rem, 4vw, 2.5rem);
    --zolo-package-title-color: #292734;
    --zolo-package-title-margin: 0 0 20px;
    --zolo-price-info-gap: 6px;
    --zolo-user-month-color: #494754;
    --zolo-price-color: #17171a;
    --zolo-original-price-padding: 0 0 0 8px;
    --zolo-package-desc-margin: 20px 0 0;
    --zolo-package-desc-color: #494754;
    --zolo-package-feature-desc-color: #494754;
    --zolo-link-btn-gap: 10px;
    --zolo-link-btn-margin: clamp(1.5rem, 4vw, 2.5rem) 0 0;
    --zolo-link-btn-padding: 12px;
    --zolo-link-btn-border-radius: 5px;
    --zolo-link-btn-border-width: 2px;
    --zolo-link-btn-border-style: solid;
    --zolo-link-btn-border-color: var(--zoloblocks-brand-color);
    --zolo-features-info-border-color: #ddd;
    --zolo-features-info-border-width: 1px;
    --zolo-features-info-border-style: solid;
    --zolo-features-info-padding: clamp(1.5rem, 4vw, 2.5rem);
    --zolo-features-desc-margin: 10px 0 0;
    --zolo-features-margin: 20px 0 0;
    --zolo-features-padding: 0;
    --zolo-features-gap: 15px;
    --zolo-feature-list-color: #494754;
    --zolo-feature-list-gap: 10px;
    --zolo-feature-list-icon-size: 13px;
    --zolo-check-icon-size: 25px;
    --zolo-ribbon-btn-margin: 20px;
    --zolo-ribbon-btn-padding: 4px 10px;

    .zolo-item {
        border-radius: var(--zolo-item-border-radius);
        background-color: var(--zoloblocks-item-bg-color);
        border-width: var(--zolo-item-wrap-border-width);
        border-style: var(--zolo-item-wrap-border-style);
        border-color: var(--zolo-item-wrap-border-color);
    }

    .zolo-head-content {
        padding: var(--zolo-head-content-padding);
        position: relative;
    }
    .zolo-package-title {
        margin: var(--zolo-package-title-margin);
        font-size: clamp(1.5rem, 3vw, 1.75rem);
        font-weight: 700;
        color: var(--zolo-package-title-color);
        text-transform: capitalize;
        line-height: 1.3;
    }

    span.zolo-package-title {
        display: inline-block !important;
    }
    .zolo-price-info {
        display: inline-flex;
        align-items: center;
        gap: var(--zolo-price-info-gap);
    }

    .zolo-user-month {
        display: flex;
        font-size: 0.875rem;
        font-weight: 300;
        color: var(--zolo-user-month-color);
        flex-direction: column;
    }
    .zolo-price {
        font-size: 3.125rem;
        font-weight: 700;
        color: var(--zolo-price-color);
        line-height: 1;
        .currency,
        .fractional {
            position: relative;
        }
    }

    .zolo-orginal-price {
        text-decoration: line-through;
        padding: var(--zolo-original-price-padding);
    }

    .zolo-package-desc {
        margin: var(--zolo-package-desc-margin);
    }

    .zolo-package-desc,
    .zolo-features-desc {
        font-size: 0.875rem;
        font-weight: 400;
    }
    .zolo-package-desc {
        color: var(--zolo-package-desc-color);
    }
    .zolo-features-desc {
        color: var(--zolo-package-feature-desc-color);
    }

    .zolo-link-btn {
        display: flex;
        gap: var(--zolo-link-btn-gap);
        text-align: center;
        margin: var(--zolo-link-btn-margin);

        &.row {
            flex-direction: row;
            align-items: center;
            .zolo-buy-btn,
            .zolo-chat-btn {
                width: 100%;
            }
        }
        &.column {
            flex-direction: column;
        }

        a {
            &.zolo-buy-btn,
            &.zolo-chat-btn {
                padding: var(--zolo-link-btn-padding);
                font-size: 0.875rem;
                border-radius: var(--zolo-link-btn-border-radius);
                color: var(--zoloblocks-brand-button-color);
                border-width: var(--zolo-link-btn-border-width);
                border-style: var(--zolo-link-btn-border-style);
                border-color: var(--zolo-link-btn-border-color);
                text-transform: capitalize;
                text-decoration: none;
                transition: all 0.3s ease;
            }
            &.zolo-buy-btn {
                background-color: var(--zoloblocks-brand-color);
                &:hover {
                    color: var(--zoloblocks-brand-text-color);
                    background-color: transparent;
                }
            }
            &.zolo-chat-btn {
                color: var(--zoloblocks-brand-text-color);
                &:hover {
                    color: var(--zoloblocks-brand-button-color);
                    background-color: var(--zoloblocks-brand-color);
                }
            }
        }
    }

    .zolo-features-info {
        border-top-width: var(--zolo-features-info-border-width);
        border-top-style: var(--zolo-features-info-border-style);
        border-top-color: var(--zolo-features-info-border-color);
        padding: var(--zolo-features-info-padding);
    }

    .zolo-features-title {
        font-size: clamp(1.125rem, 2vw, 1.25rem);
        font-weight: 700;
        text-transform: uppercase;
    }

    .zolo-features-desc {
        margin: var(--zolo-features-desc-margin);
    }

    .zolo-item {
        .features {
            margin: var(--zolo-features-margin);
            padding: var(--zolo-features-padding);
            list-style: none;
            display: flex;
            flex-direction: column;
            gap: var(--zolo-features-gap);
        }
    }

    .features li {
        display: flex;
        align-items: center;
        font-size: 0.875rem;
        gap: var(--zolo-feature-list-gap);
        color: var(--zolo-feature-list-color);
        svg {
            width: var(--zolo-feature-list-icon-size);
            height: var(--zolo-feature-list-icon-size);
            fill: var(--zoloblocks-brand-button-color);
            display: block;
        }
    }

    .zolo-check-icon {
        background-color: var(--zoloblocks-brand-color);
        border-radius: 50%;
        line-height: 1;
        width: var(--zolo-check-icon-size);
        height: var(--zolo-check-icon-size);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 0.75rem;
    }

    .zolo-ribbon-btn {
        position: absolute;
        top: 0;
        margin: var(--zolo-ribbon-btn-margin);
        color: var(--zoloblocks-brand-button-color);
        font-size: 0.75rem;
        text-transform: capitalize;
        background-color: var(--zoloblocks-brand-color);
        padding: var(--zolo-ribbon-btn-padding);
        border-radius: var(--zoloblocks-button-border-radius);
        &.top__right {
            right: 0;
            left: auto;
        }
        &.top__left {
            left: 0;
            right: auto;
        }
    }

    .zolo-featerd-item {
        background-color: #000;
    }

    .zolo-featerd-item .zolo-buy-btn,
    .zolo-featerd-item .zolo-chat-btn:hover {
        background-color: var(--zoloblocks-brand-color);
        color: #000;
        border: 1px solid #ddd;
    }

    .zolo-featerd-item .zolo-buy-btn:hover,
    .zolo-featerd-item .zolo-chat-btn {
        background-color: white;
        color: #000;
    }

    .zolo-featerd-item .zolo-package-title {
        color: #72fd3f;
    }

    .zolo-featerd-item .zolo-package-title.span {
        display: inline-block;
    }

    .zolo-featerd-item .zolo-price,
    .zolo-featerd-item .zolo-features-title {
        color: #e1e1e1;
    }

    .zolo-featerd-item .zolo-user-month,
    .zolo-featerd-item .zolo-package-desc,
    .zolo-featerd-item .zolo-features-desc,
    .zolo-featerd-item .features li {
        color: #b4b5b9;
    }

    .zolo-featerd-item .zolo-check-icon {
        color: #000;
    }

    .zolo-featerd-item .zolo-features-info {
        border-top: 1px solid rgba(215, 218, 223, 0.322);
    }
}
